html,body,ul,li,span,a,p,img,tr,td,h1,h2,h3,h4,h5,h6 {
    padding: 0;
    margin: 0;
}

html,body {
    width: 100%;
    height: 100%;
    font-family: 'Brandon Grotesque", Helvetica, Arial, sans-serif';
}

ul,li {
    list-style-type: none;
}

a {
   text-decoration: none;
}

#root {
  height: 100%;
}

.left {
    float: left;
}

.right {
    float: right;
}

.clear {
    clear: both;
}

.drip {
    width: 100%;
    height: 100%;
    display: flex;
    min-width: 1920px;
    max-width: 1920px;
    margin: 0 auto;
}

.drip_left {
    flex: 0.3 1;
    height: 100%;
    border-right: 1px solid #ccc;
}

.drip_right {
    flex: 0.7 1;
    height: 100%;
    overflow: auto;
    background: #fff;
    position: relative;
}

.drip_l_nav {
    width: 100%;
    height: 80px;
}

.drip_l_nav li {
    width: 10%;
    float: left;
    text-align: center;
    line-height: 80px;
    font-weight: bold;
    cursor: pointer;
}

.drip_l_nav li a {
    color: #212121;
}

.drip_l_nav #portrait {
    height: 40px;
    position: relative;
    margin-right: 20px;
}

.drip_l_nav #portrait img {
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 40px;
    overflow: hidden;
}

.drip_l_nav #menu {
    display: block;
    width: 30px;
    height: 30px;
    float: right;
    margin: 22px 15px 0 0;
    background: url(data:image/png;base64,) no-repeat center center;
    background-size: 25px 25px;
    position: relative;
}

.drip_l_content {
    margin-top: 60%;
    text-align: center;
}

.drip_l_content h2 {
    text-align: center;
    margin-bottom: 10px;
}

.drip_l_content h2 img {
    width: 35px;
    height: 20px;
    margin-top: 5px;
}

.drip_l_content span {
    margin-top: 25px;
    font-size: 14px;
    color: #61C6F1;
}

.drip_right .drip_r_content {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.drip_right .drip_r_content .art_list {
    width: 80%;
    height: 150px;
    overflow: hidden;
    padding:30px 0px;
    margin: 0 auto;
    margin-top: 10px;
    border-bottom:1px solid #ccc; 
    cursor: pointer;
    background: #fff;
}
.drip_right .drip_r_content .art_list .list-text {
    width: 70%;
    float: left;
}

.drip_right .drip_r_content .art_list .list-img {
    width: 23%;
    height: 125px;
    float: right;
    margin-top: 30px;
    overflow: hidden;
}

.drip_right .drip_r_content .art_list .list-img img {
    width: 245px;
    height: 125px;
}

.drip_right .drip_r_content .art_list h3 {
    text-align: left;
    margin-top: 30px;
    margin-bottom: 15px;
}

.drip_right .drip_r_content .art_list h3 a {
    color: #36342F;
}

.drip_right .drip_r_content .art_list span {
    color: #5FC0EA;
    text-align: left;
    font-size: 14px;
}

.drip_left .menuList {
    width: 300px;
    display: inline-block;
    border: 1px solid #fff;
    position: absolute;
    left: -150px;
    top: 30px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    background: #fff;
    z-index: 10;
}

.drip_left .menuList li {
    width: 100%;
}

.drip_left .menuList li:hover {
    background: #61C6F1;
    color: #fff;
}

.page {
    height: 100px;
    width: 100%;
    position: relative;
}

.page_ul {
    position: absolute;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.page .page_num {
    width:15p;
    height:15px;
    border:1px solid #61C6F1;
    float:left;
    padding: 6px 8px;
    line-height: 15px;
    cursor: pointer;
    margin: 0 5px;
    border-radius: 3px;
    font-size: 14px;
}

.page .page_num:hover{
    background: #5FC0EA;
    color: #fff;
}

#active {
    background: #5FC0EA;
    color: #fff;    
}

.content {
    overflow-x: hidden;
    padding: 60px 30px;
}

.content .title {
    text-align: center;
    margin-bottom: 30px;
}

.content .msg {
    margin: 0 auto;
}

.content .msg span{
    display: block;
}

.content .text {
    clear: both;
    margin-top: 80px;
}


.from {
    width: 500px;
    height: 500px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -250px -250px;
}

.from input {
    width: 400px;
    height: 50px;
    display: block;
    margin: 50px auto;
    font-size: 18px;
    line-height: 50px;
    padding: 0 10px;
    border: 0px;
    border-bottom: 1px solid #DDDDDD;
    outline: none;
    text-align: center;
}

.from button {
    width: 100px;
    height: 50px;
    border: 1px solid #ddd;
    box-shadow: 1px 1px 1px 1px #ddd;
    border-radius: 50px;
    background: #fff;
    display: block;
    margin: 0 auto;
    line-height: 50px;
    text-align: center;
    font-size: 16px;
    color: #999999;
    font-weight: bold;
    cursor: pointer;
    outline: none;
}

.from .msg {
    color: red;
    text-align: center;
    display: block;
    width: 80%;
    overflow: hidden;
    margin: 0 auto;
    margin-top: -30px;
    margin-bottom: 10px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
}
.admin {
    padding: 30px;
}

.username {
    padding-left: 20px;
    margin-right: 15px;
}

.out {
    background: #fff;
    border: 0px;
    cursor: pointer;
    font-size: 16px;
}

.out:hover {
    color:red;
}

.pad {
    padding: 0 10px;
}

.btn {
    cursor: pointer;
}

table tr td .btn:hover {
    color: #5EBDE5;
}

.alert {
    width: 300px;
    height: 200px;
    border: 1px solid #ddd;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -150px -150px;
    background: #ddd;
}

.alert .msg {
    width: 100%;
    text-align: center;
    font-size: 16px;
    margin-top: 25%;
}

.alert .btn {
    width: 100%;
    margin-top: 10%;
    text-align: center;
}

.alert .btn span {
    padding: 0 15px;
}
.error {
    width: 95%;
    height: 300px;
    position: absolute;
    top: 50%;
    margin: -150px auto;
}

.error_img {
    display: block;
    float: left;
    margin-left: 30px;
    margin-top: 100px;
}

.error_msg {
    width: 85%;
    height: 120px;
    float: left;
    margin-top: 100px;
    line-height: 120px;
    font-size: 16px;
    text-align: left;
    padding-left: 20px;
    color: #666666;
}

.msg_text {
    font-weight: bold;
    color: black;
    font-size: 18px;
}

.url_s {
    color: black;
    padding-left: 15px;
}
.add {
    padding: 30px;
}

.input_text {
    width: 300px;
    height: 30px;
    padding-left: 15px;
    font-size: 16px;
    text-align: left;
    outline: none;
}

.input_tit {
    font-size: 18px;
    width: 80px;
}

.rdw-editor-main {
    border: 1px solid #F1F1F1;
    min-height: 300px;
    max-width: 1200px;
    padding: 10px 20px;
}

.btn {
    text-align: center;
}

.btn button {
    border: 1px solid #40a9ff;
    margin: 0 20px;
    color: #40a9ff;
    padding: 8px 20px;
    border-radius: 3px;
    background: none;
    cursor: pointer;
}

.btn button:hover {
    background: #40a9ff;
    color: #fff;
}

select {
    height: 30px;
    font-size: 16px;
    width: 150px;
}

select option {
    text-align: center;
}

