.add {
    padding: 30px;
}

.input_text {
    width: 300px;
    height: 30px;
    padding-left: 15px;
    font-size: 16px;
    text-align: left;
    outline: none;
}

.input_tit {
    font-size: 18px;
    width: 80px;
}

.rdw-editor-main {
    border: 1px solid #F1F1F1;
    min-height: 300px;
    max-width: 1200px;
    padding: 10px 20px;
}

.btn {
    text-align: center;
}

.btn button {
    border: 1px solid #40a9ff;
    margin: 0 20px;
    color: #40a9ff;
    padding: 8px 20px;
    border-radius: 3px;
    background: none;
    cursor: pointer;
}

.btn button:hover {
    background: #40a9ff;
    color: #fff;
}

select {
    height: 30px;
    font-size: 16px;
    width: 150px;
}

select option {
    text-align: center;
}
