.admin {
    padding: 30px;
}

.username {
    padding-left: 20px;
    margin-right: 15px;
}

.out {
    background: #fff;
    border: 0px;
    cursor: pointer;
    font-size: 16px;
}

.out:hover {
    color:red;
}

.pad {
    padding: 0 10px;
}

.btn {
    cursor: pointer;
}

table tr td .btn:hover {
    color: #5EBDE5;
}

.alert {
    width: 300px;
    height: 200px;
    border: 1px solid #ddd;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -150px -150px;
    background: #ddd;
}

.alert .msg {
    width: 100%;
    text-align: center;
    font-size: 16px;
    margin-top: 25%;
}

.alert .btn {
    width: 100%;
    margin-top: 10%;
    text-align: center;
}

.alert .btn span {
    padding: 0 15px;
}