.error {
    width: 95%;
    height: 300px;
    position: absolute;
    top: 50%;
    margin: -150px auto;
}

.error_img {
    display: block;
    float: left;
    margin-left: 30px;
    margin-top: 100px;
}

.error_msg {
    width: 85%;
    height: 120px;
    float: left;
    margin-top: 100px;
    line-height: 120px;
    font-size: 16px;
    text-align: left;
    padding-left: 20px;
    color: #666666;
}

.msg_text {
    font-weight: bold;
    color: black;
    font-size: 18px;
}

.url_s {
    color: black;
    padding-left: 15px;
}