.from {
    width: 500px;
    height: 500px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -250px -250px;
}

.from input {
    width: 400px;
    height: 50px;
    display: block;
    margin: 50px auto;
    font-size: 18px;
    line-height: 50px;
    padding: 0 10px;
    border: 0px;
    border-bottom: 1px solid #DDDDDD;
    outline: none;
    text-align: center;
}

.from button {
    width: 100px;
    height: 50px;
    border: 1px solid #ddd;
    box-shadow: 1px 1px 1px 1px #ddd;
    border-radius: 50px;
    background: #fff;
    display: block;
    margin: 0 auto;
    line-height: 50px;
    text-align: center;
    font-size: 16px;
    color: #999999;
    font-weight: bold;
    cursor: pointer;
    outline: none;
}

.from .msg {
    color: red;
    text-align: center;
    display: block;
    width: 80%;
    overflow: hidden;
    margin: 0 auto;
    margin-top: -30px;
    margin-bottom: 10px;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
}