.left {
    float: left;
}

.right {
    float: right;
}

.clear {
    clear: both;
}

.drip {
    width: 100%;
    height: 100%;
    display: flex;
    min-width: 1920px;
    max-width: 1920px;
    margin: 0 auto;
}

.drip_left {
    flex: 0.3;
    height: 100%;
    border-right: 1px solid #ccc;
}

.drip_right {
    flex: 0.7;
    height: 100%;
    overflow: auto;
    background: #fff;
    position: relative;
}

.drip_l_nav {
    width: 100%;
    height: 80px;
}

.drip_l_nav li {
    width: 10%;
    float: left;
    text-align: center;
    line-height: 80px;
    font-weight: bold;
    cursor: pointer;
}

.drip_l_nav li a {
    color: #212121;
}

.drip_l_nav #portrait {
    height: 40px;
    position: relative;
    margin-right: 20px;
}

.drip_l_nav #portrait img {
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 40px;
    overflow: hidden;
}

.drip_l_nav #menu {
    display: block;
    width: 30px;
    height: 30px;
    float: right;
    margin: 22px 15px 0 0;
    background: url('image/menu.png') no-repeat center center;
    background-size: 25px 25px;
    position: relative;
}

.drip_l_content {
    margin-top: 60%;
    text-align: center;
}

.drip_l_content h2 {
    text-align: center;
    margin-bottom: 10px;
}

.drip_l_content h2 img {
    width: 35px;
    height: 20px;
    margin-top: 5px;
}

.drip_l_content span {
    margin-top: 25px;
    font-size: 14px;
    color: #61C6F1;
}

.drip_right .drip_r_content {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.drip_right .drip_r_content .art_list {
    width: 80%;
    height: 150px;
    overflow: hidden;
    padding:30px 0px;
    margin: 0 auto;
    margin-top: 10px;
    border-bottom:1px solid #ccc; 
    cursor: pointer;
    background: #fff;
}
.drip_right .drip_r_content .art_list .list-text {
    width: 70%;
    float: left;
}

.drip_right .drip_r_content .art_list .list-img {
    width: 23%;
    height: 125px;
    float: right;
    margin-top: 30px;
    overflow: hidden;
}

.drip_right .drip_r_content .art_list .list-img img {
    width: 245px;
    height: 125px;
}

.drip_right .drip_r_content .art_list h3 {
    text-align: left;
    margin-top: 30px;
    margin-bottom: 15px;
}

.drip_right .drip_r_content .art_list h3 a {
    color: #36342F;
}

.drip_right .drip_r_content .art_list span {
    color: #5FC0EA;
    text-align: left;
    font-size: 14px;
}

.drip_left .menuList {
    width: 300px;
    display: inline-block;
    border: 1px solid #fff;
    position: absolute;
    left: -150px;
    top: 30px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);
    background: #fff;
    z-index: 10;
}

.drip_left .menuList li {
    width: 100%;
}

.drip_left .menuList li:hover {
    background: #61C6F1;
    color: #fff;
}

.page {
    height: 100px;
    width: 100%;
    position: relative;
}

.page_ul {
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%,-50%);
}

.page .page_num {
    width:15p;
    height:15px;
    border:1px solid #61C6F1;
    float:left;
    padding: 6px 8px;
    line-height: 15px;
    cursor: pointer;
    margin: 0 5px;
    border-radius: 3px;
    font-size: 14px;
}

.page .page_num:hover{
    background: #5FC0EA;
    color: #fff;
}

#active {
    background: #5FC0EA;
    color: #fff;    
}
